import React from 'react';
import './App.css';
import Header from './Header';
import Home from './Home';
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import Checkout from './Checkout';
import Subtotal from './Subtotal';


function App() {

  return (
    <Router>
    <div className="App">
      <Header />

      <Switch>
      
      
      <Route path="/login">

      </Route>


      <Route path="/checkout">
        <Checkout />
      </Route>

      <Route path="/">
        <Home />
      </Route>

      </Switch>
      
    </div>
    </Router>
  );
}

export default App;
